lor: $primary-color;\n$link-hover: $gray;\n$button-color: $primary-color;\n$button-hover: black;\n$body-color: $black;\n$border-color: $gray-light;\n$overlay: rgba(25, 25, 25, 0.6);\n\n/**\n * Typography\n */\n$font: Georgia, Times, \"Times New Roman\", serif;\n$font-primary: \"Raleway\", sans-serif;\n$font-secondary: \"Bromello\", Georgia, Times, \"Times New Roman\", serif;\n$sans-serif: \"Helvetica\", \"Arial\", sans-serif;\n$serif: Georgia, Times, \"Times New Roman\", serif;\n$monospace: Menlo, Monaco, \"Courier New\", \"Courier\", monospace;\n\n// Questa font weights: 400 700 900\n\n/**\n * Amimation\n */\n$cubic-bezier: cubic-bezier(0.885, -0.065, 0.085, 1.02);\n$ease-bounce: cubic-bezier(0.3, -0.14, 0.68, 1.17);\n\n/**\n * Default Spacing/Padding\n */\n$space: 1.25rem;\n$space-and-half: $space*1.5;\n$space-double: $space*2;\n$space-quad: $space*4;\n$space-half: $space/2;\n$pad: 1.25rem;\n$pad-and-half: $pad*1.5;\n$pad-double: $pad*2;\n$pad-half: $pad/2;\n$pad-quarter: $pad/4;\n$pad-triple: $pad*3;\n$pad-quad: $pad*4;\n$gutters: (mobile: 10, desktop: 10, super: 10);\n$verticalspacing: (mobile: 20, desktop: 30);\n\n/**\n * Icon Sizing\n */\n$icon-xsmall: rem(15);\n$icon-small: rem(20);\n$icon-medium: rem(30);\n$icon-large: rem(50);\n$icon-xlarge: rem(80);\n\n/**\n * Common Breakpoints\n */\n$xsmall: 350px;\n$small: 500px;\n$medium: 700px;\n$large: 900px;\n$xlarge: 1100px;\n$xxlarge: 1300px;\n$xxxlarge: 1500px;\n\n$breakpoints: (\n  'xsmall': $xsmall,\n  'small': $small,\n  'medium': $medium,\n  'large': $large,\n  'xlarge': $xlarge,\n  'xxlarge': $xxlarge,\n  'xxxlarge': $xxxlarge\n);\n\n/**\n * Element Specific Dimensions\n */\n$article-max: rem(950);\n$sidebar-width: 320;\n$utility-header-height: 40;\n$small-header-height: 60;\n$large-header-height: 150;\n","/* ------------------------------------*\\\n    $MEDIA QUERY TESTS\n\\*------------------------------------ */\n@if $tests == true {\n  body {\n    &::before {\n      display: block;\n      position: fixed;\n      z-index: 100000;\n      background: black;\n      bottom: 0;\n      right: 0;\n      padding: 0.5em 1em;\n      content: 'No Media Query';\n      color: transparentize(#fff, 0.25);\n      border-top-left-radius: 10px;\n      font-size: (12/16)+em;\n\n      @media print {\n        display: none;\n      }\n    }\n\n    &::after {\n      display: block;\n      position: fixed;\n      height: 5px;\n      bottom: 0;\n      left: 0;\n      right: 0;\n      z-index: (100000);\n      content: '';\n      background: black;\n\n      @media print {\n        display: none;\n      }\n    }\n\n    @include media('>xsmall') {\n      &::before {\n        content: 'xsmall: 350px';\n      }\n\n      &::after,\n      &::before {\n        background: dodgerblue;\n      }\n    }\n\n    @include media('>small') {\n      &::before {\n        content: 'small: 500px';\n      }\n\n      &::after,\n      &::before {\n        background: darkseagreen;\n      }\n    }\n\n    @include media('>medium') {\n      &::before {\n        content: 'medium: 700px';\n      }\n\n      &::after,\n      &::before {\n        background: lightcoral;\n      }\n    }\n\n    @include media('>large') {\n      &::before {\n        content: 'large: 900px';\n      }\n\n      &::after,\n      &::before {\n        background: mediumvioletred;\n      }\n    }\n\n    @include media('>xlarge') {\n      &::before {\n        content: 'xlarge: 1100px';\n      }\n\n      &::after,\n      &::before {\n        background: hotpink;\n      }\n    }\n\n    @include media('>xxlarge') {\n      &::before {\n        content: 'xxlarge: 1300px';\n      }\n\n      &::after,\n      &::before {\n        background: orangered;\n      }\n    }\n\n    @include media('>xxxlarge') {\n      &::before {\n        content: 'xxxlarge: 1400px';\n      }\n\n      &::after,\n      &::before {\n        background: dodgerblue;\n      }\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $RESET\n\\*---------------------------------