orm: scale(1.1);\n    }\n\n    span {\n      transition: all 0.25s ease;\n      margin: 0 auto;\n\n      svg path {\n        fill: $black;\n      }\n    }\n\n    &::after {\n      display: none;\n    }\n  }\n}\n\nheader .form__search {\n  position: relative;\n  border: none;\n\n  input[type=text] {\n    color: white;\n    font-size: rem(14);\n    width: rem(110);\n    padding-left: rem($utility-header-height);\n\n    /* Chrome/Opera/Safari */\n    &::-webkit-input-placeholder {\n      color: $white;\n\n      @include font--primary--xs;\n    }\n\n    /* Firefox 19+ */\n    &::-moz-placeholder {\n      color: $white;\n\n      @include font--primary--xs;\n    }\n\n    /* IE 10+ */\n    &:-ms-input-placeholder {\n      color: $white;\n\n      @include font--primary--xs;\n    }\n\n    /* Firefox 18- */\n    &:-moz-placeholder {\n      color: $white;\n\n      @include font--primary--xs;\n    }\n  }\n\n  input[type=text]:focus,\n  &:hover input[type=text],\n  input[type=text]:not(:placeholder-shown) {\n    width: 100%;\n    min-width: rem(200);\n    background-color: rgba(black, 0.8);\n\n    @include media('>large') {\n      width: rem(200);\n      min-width: none;\n    }\n  }\n\n  button {\n    position: absolute;\n    left: 0;\n    width: rem($utility-header-height);\n    height: rem($utility-header-height);\n\n    span {\n      svg path {\n        fill: $white;\n      }\n    }\n  }\n}\n\n.search-form {\n  max-width: rem(400);\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  flex-direction: row;\n  flex-wrap: nowrap;\n\n  label {\n    font-size: inherit;\n    margin: 0;\n    padding: 0;\n  }\n\n  .search-field {\n    font-size: inherit;\n    padding: $pad-half;\n  }\n\n  .search-submit {\n    border-radius: 0;\n    padding: $pad-half;\n    margin-top: 0;\n    border: 1px solid transparent;\n  }\n}\n\nlabel {\n  margin-bottom: rem(5);\n\n  @include font--primary--xs;\n}\n\n.wpcf7-form {\n  label {\n    margin-bottom: $space-half;\n  }\n\n  .wpcf7-list-item {\n    width: 100%;\n    margin-top: $space;\n    margin-left: 0;\n\n    &:first-child {\n      margin-top: 0;\n    }\n\n    &.first {\n      margin-top: $space-half;\n    }\n  }\n\n  input[type=submit] {\n    margin: $space auto 0 auto;\n  }\n}\n","/* Slider */\n.slick-slider {\n  position: relative;\n  display: block;\n  box-sizing: border-box;\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n  -ms-touch-action: pan-y;\n  touch-action: pan-y;\n  -webkit-tap-highlight-color: transparent;\n}\n\n.slick-list {\n  position: relative;\n  overflow: hidden;\n  display: block;\n  margin: 0;\n  padding: 0;\n\n  &:focus {\n    outline: none;\n  }\n\n  &.dragging {\n    cursor: pointer;\n    cursor: hand;\n  }\n}\n\n.slick-slider .slick-track,\n.slick-slider .slick-list {\n  -webkit-transform: translate3d(0, 0, 0);\n  -moz-transform: translate3d(0, 0, 0);\n  -ms-transform: translate3d(0, 0, 0);\n  -o-transform: translate3d(0, 0, 0);\n  transform: translate3d(0, 0, 0);\n}\n\n.slick-track {\n  position: relative;\n  left: 0;\n  top: 0;\n  display: block;\n  height: 100%;\n\n  &::before,\n  &::after {\n    content: \"\";\n    display: table;\n  }\n\n  &::after {\n    clear: both;\n  }\n\n  .slick-loading & {\n    visibility: hidden;\n  }\n}\n\n.slick-slide {\n  float: left;\n  height: 100%;\n  min-height: 1px;\n\n  [dir=\"rtl\"] & {\n    float: right;\n  }\n\n  img {\n    display: block;\n  }\n\n  &.slick-loading img {\n    display: none;\n  }\n\n  display: none;\n\n  &.dragging img {\n    pointer-events: none;\n  }\n\n  &:focus {\n    outline: none;\n  }\n\n  .slick-initialized & {\n    display: block;\n  }\n\n  .slick-loading & {\n    visibility: hidden;\n  }\n\n  .slick-vertical & {\n    display: block;\n    height: auto;\n    border: 1px solid transparent;\n  }\n}\n\n.slick-arrow.slick-hidden {\n  display: none;\n}\n\n.slick-disabled {\n  opacity: 0.5;\n}\n\n.slick-dots {\n  position: relative;\n  height: rem(40);\n  line-height: rem(40);\n  width: 100%;\n  list-style: none;\n  text-align: center;\n\n  li {\n    position: relative;\n    display: inline-block;\n    margin: 0 !important;\n    padding: 0 rem(5) !important;\n    cursor: pointer;\n    list-style: none;\n\n    &::before {\n      display: none !important;\n    }\n\n    button {\n      padding: 0;\n      border-radius: rem(50);\n      border: 0;\n      display: block;\n      height: rem(10);\n      width: rem(10);\n      outline: none;\n      line-height: 0;\n      font-size: 0;\n      color: transparent;\n      background: $gray;\n    }\n\n    &.slick-active {\n      button {\n        background-color: $black;\n      }\n    }\n  }\n}\n\n.slick-arrow {\n  padding: $pad-and-half;\n  cursor: pointer;\n  transition: all 0.25s ease;\n\n  &:hover {\n    opacity: 1;\n  }\n}\n\n.slick-gallery {\n  margin-left: -$space;\n  margin-right: -$space;\n  width: calc(100% + 40px);\n  position: relative;\n\n  @include media('>medium') {\n    margin-left: 0;\n    margin-right: 0;\n    width: 100%;\n  }\n\n  .slick-track {\n    height: auto;\n\n    img {\n      width: auto;\n      margin: 0 auto;\n      max-height: calc(100vh - 60px);\n    }\n  }\n\n  .slick-arrow {\n    position: absolute;\n    z-index: 99;\n    top: calc(50% - 20px);\n    transform: translateY(calc(-50% - 20px));\n    opacity: 0.5;\n\n    &:hover {\n      opacity: 1;\n    }\n\n    &.icon--arrow-prev {\n      left: 0;\n      transform: translateY(-50%) rotate(180deg);\n      background-position: center center;\n    }\n\n    &.icon--arrow-next {\n      right: 0;\n      transform: translateY(-50%);\n      background-position: center center;\n    }\n  }\n\n  img {\n    width: 100%;\n    height: auto;\n  }\n}\n\n.slick-favorites {\n  display: flex;\n  flex-wrap: nowrap;\n  flex-direction: row;\n\n  .slick-slide {\n    padding: rem(5);\n\n    @include media('>medium') {\n      padding: $space-half;\n    }\n  }\n}\n\n.touch .slick-gallery .slick-arrow {\n  display: none !important;\n}\n\n.slick-arrow {\n  position: relative;\n  background-size: auto rem(35);\n  background-position: center center;\n  padding: $pad;\n\n  @include media('>medium') {\n    background-size: auto rem(50);\n    padding: $pad-and-half;\n  }\n}\n\n.slick-disabled {\n  opacity: 0.25;\n}\n\n@keyframes rotateWord {\n  0% {\n    opacity: 0;\n  }\n\n  2% {\n    opacity: 0;\n    transform: translateY(-30px);\n  }\n\n  5% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n\n  17% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n\n  20% {\n    opacity: 0;\n    transform: translateY(30px);\n  }\n\n  80% {\n    opacity: 0;\n  }\n\n  100% {\n    opacity: 0;\n  }\n}\n\n.rw-wrapper {\n  width: 100%;\n  display: block;\n  position: relative;\n  margin-top: $space*3;\n\n  @include media('>small') {\n    margin-top: $space;\n  }\n}\n\n.rw-words {\n  display: inline-block;\n  margin: 0 auto;\n  text-align: center;\n  position: relative;\n  width: 100%;\n\n  span {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    left: 0;\n    opacity: 0;\n    animation: rotateWord 18s linear infinite 0s;\n    font-size: rem(13);\n    letter-spacing: 1.5px;\n    font-weight: bold;\n    white-space: nowrap;\n\n    @include media('>small') {\n      font-size: rem(16);\n    }\n  }\n\n  span:nth-child(2) {\n    animation-delay: 3s;\n  }\n\n  span:nth-child(3) {\n    animation-delay: 6s;\n  }\n\n  span:nth-child(4) {\n    animation-delay: 9s;\n  }\n\n  span:nth-child(5) {\n    animation-delay: 12s;\n  }\n\n  span:nth-child(6) {\n    animation-delay: 15s;\n  }\n}\n","/* ------------------------------------*\\\n    $ARTICLE\n\\*------------------------------------ */\n\n.article__picture {\n  img {\n    margin: 0 auto;\n    display: block;\n  }\n}\n\n.article__categories {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  border-top: 1px solid $gray;\n  border-bottom: 1px solid $gray;\n  padding: $pad;\n\n  @include media('>medium') {\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n  }\n}\n\n.article__category {\n  display: flex;\n  flex-direction: row;\n  text-align: left;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n\n  > * {\n    width: 50%;\n  }\n\n  span {\n    padding-right: $pad;\n    min-width: rem(120);\n    text-align: right;\n  }\n\n  @include media('>medium') {\n    flex-direction: column;\n    text-align: center;\n    width: auto;\n\n    > * {\n      width: auto;\n    }\n\n    span {\n      padding-right: 0;\n      text-align: center;\n      margin-bottom: rem(5);\n    }\n  }\n}\n\n.article__content--left {\n  .divider {\n    margin: $space-half auto;\n  }\n}\n\n.article__content--right {\n  height: auto;\n\n  .yarpp-related {\n    display: none;\n  }\n}\n\n.article__image {\n  margin-left: -$space;\n  margin-right: -$space;\n\n  @include media('>medium') {\n    margin-left: 0;\n    margin-right: 0;\n  }\n}\n\n.article__toolbar {\n  position: fixed;\n  bottom: 0;\n  margin: 0;\n  left: 0;\n  width: 100%;\n  height: rem(40);\n  background: white;\n  padding: 0 $pad;\n  z-index: 9999;\n\n  @include media('>medium') {\n    display: none;\n  }\n\n  .block__toolbar--right {\n    display: flex;\n    align-items: center;\n\n    a {\n      line-height: rem(40);\n    }\n\n    .icon {\n      width: rem(10);\n      height: rem(20);\n      position: relative;\n      top: rem(1);\n      margin-left: $space-half;\n    }\n  }\n\n  .block__toolbar-item.block__toolbar-share {\n    padding-right: $pad-half;\n  }\n}\n\n.article__share {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n  text-align: center;\n}\n\n.article__share-link {\n  transition: all 0.25s ease;\n  margin-left: auto;\n  margin-right: auto;\n\n  &:hover {\n    transform: scale(1.1);\n  }\n}\n\n.article__share-work > div {\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n\n  a {\n    margin-top: 0;\n    margin-left: $space-half;\n    margin-right: $space-half;\n  }\n}\n\n.article__nav {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  flex-wrap: nowrap;\n}\n\n.article__nav--inner {\n  width: calc(50% - 10px);\n  text-align: center;\n\n  @include media('>large') {\n    width: calc(50% - 20px);\n  }\n}\n\n.article__nav-item {\n  width: 100%;\n  text-align: center;\n\n  &.previous {\n    .icon {\n      float: left;\n    }\n  }\n\n  &.next {\n    .icon {\n      float: right;\n    }\n  }\n}\n\n.article__nav-item-label {\n  position: relative;\n  height: rem(28.8);\n  line-height: rem(28.8);\n  margin-bottom: $space-half;\n\n  .icon {\n    z-index: 2;\n    height: rem(28.8);\n    width: rem(15);\n  }\n\n  font {\n    background: $background-color;\n    padding-left: $pad-half;\n    padding-right: $pad-half;\n    z-index: 2;\n  }\n\n  &::after {\n    width: 100%;\n    height: rem(1);\n    background-color: $black;\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n    left: 0;\n    content: \"\";\n    display: block;\n    z-index: -1;\n  }\n}\n\nol,\nul {\n  .article__body & {\n    margin-left: 0;\n\n    li {\n      list-style: none;\n      padding-left: $pad;\n      text-indent: rem(-10);\n\n      &::before {\n        color: $primary-color;\n        width: rem(10);\n        display: inline-block;\n      }\n\n      li {\n        list-style: none;\n      }\n    }\n  }\n}\n\nol {\n  .article__body & {\n    counter-reset: item;\n\n    li {\n      &::before {\n        content: counter(item) \". \";\n        counter-increment: item;\n      }\n\n      li {\n        counter-reset: item;\n\n        &::before {\n          content: \"\\002010\";\n        }\n      }\n    }\n  }\n}\n\nul {\n  .article__body & {\n    li {\n      &::before {\n        content: \"\\002022\";\n      }\n\n      li {\n        &::before {\n          content: \"\\0025E6\";\n        }\n      }\n    }\n  }\n}\n\narticle {\n  margin-left: auto;\n  margin-right: auto;\n\n  p a,\n  li a {\n    text-decoration: underline !important;\n  }\n\n  li p {\n    margin-top: $space;\n    text-indent: 0;\n  }\n}\n\nbody#tinymce,\n.article__body {\n  p,\n  ul,\n  ol,\n  dt,\n  dd {\n    @include p;\n  }\n\n  strong {\n    font-weight: bold;\n  }\n\n  > p:empty,\n  > h2:empty,\n  > h3:empty {\n    display: none;\n  }\n\n  > h1,\n  > h2,\n  > h3,\n  > h4 {\n    margin-top: $space-double;\n\n    &:first-child {\n      margin-top: 0;\n    }\n  }\n\n  h1,\n  h2 {\n    + * {\n      margin-top: $space-and-half;\n    }\n  }\n\n  h3,\n  h4,\n  h5,\n  h6 {\n    + * {\n      margin-top: $space-half;\n    }\n  }\n\n  img {\n    height: auto;\n  }\n\n  hr {\n    margin-top: $space-half;\n    margin-bottom: $space-half;\n\n    @include media('>large') {\n      margin-top: $space;\n      margin-bottom: $space;\n    }\n  }\n\n  figcaption {\n    @include font--s;\n  }\n\n  figure {\n    max-width: none;\n    width: auto !important;\n  }\n\n  .wp-caption-text {\n    display: block;\n    line-height: 1.3;\n    text-align: left;\n  }\n\n  .size-full {\n    width: auto;\n  }\n\n  .size-thumbnail {\n    max-width: rem(400);\n    height: auto;\n  }\n\n  .aligncenter {\n    margin-left: auto;\n    margin-right: auto;\n    text-align: center;\n\n    figcaption {\n      text-align: center;\n    }\n  }\n\n  @include media('>small') {\n    .alignleft,\n    .alignright {\n      min-width: 50%;\n      max-width: 50%;\n\n      img {\n        width: 100%;\n      }\n    }\n\n    .alignleft {\n      float: left;\n      margin: $space-and-half $space-and-half 0 0;\n    }\n\n    .alignright {\n      float: right;\n      margin: $space-and-half 0 0 $space-and-half;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $SIDEBAR\n\\*------------------------------------ */\n\n.widget-tags {\n  .tags {\n    display: flex;\n    flex-wrap: wrap;\n    flex-direction: row;\n\n    .tag {\n      white-space: nowrap;\n    }\n\n    .tag::before {\n      content: \",\";\n      padding-left: rem(1);\n      padding-right: rem(3);\n    }\n\n    .tag:first-child::before {\n      content: \"\";\n    }\n  }\n}\n\n.widget-mailing {\n  form {\n    input {\n      border-color: $black;\n      color: $black;\n    }\n  }\n\n  button {\n    background-color: $black;\n    color: $white;\n\n    &:hover {\n      background-color: black;\n      color: $white;\n    }\n  }\n}\n\n.widget-related {\n  .block {\n    margin-bottom: $space;\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $FOOTER\n\\*------------------------------------ */\n\n.footer {\n  position: relative;\n  display: flex;\n  flex-direction: row;\n  overflow: hidden;\n  padding: $pad-double 0 $pad 0;\n\n  @include media('>medium') {\n    margin-bottom: 0;\n  }\n\n  a {\n    color: $white;\n  }\n}\n\n.footer--inner {\n  width: 100%;\n}\n\n.footer--left {\n  @include media('>medium') {\n    width: 50%;\n  }\n\n  @include media('>xlarge') {\n    width: 33.33%;\n  }\n}\n\n.footer--right {\n  display: flex;\n  flex-direction: column;\n\n  > div {\n    @include media('>xlarge') {\n      flex-direction: row;\n      padding-right: $pad;\n    }\n  }\n\n  @include media('>medium') {\n    width: 50%;\n    flex-direction: row;\n  }\n\n  @include media('>xlarge') {\n    width: 66.67%;\n  }\n}\n\n.footer__row {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n\n  &--bottom {\n    align-items: flex-start;\n    padding-right: $pad-double;\n  }\n\n  @include media('>medium') {\n    &--top {\n      flex-direction: row;\n    }\n  }\n\n  @include media('>large') {\n    flex-direction: row;\n    justify-content: space-between;\n  }\n}\n\n.footer__nav {\n  display: flex;\n  justify-content: flex-start;\n  align-items: flex-start;\n  flex-direction: row;\n}\n\n.footer__nav-col {\n  display: flex;\n  flex-direction: column;\n  padding-right: $pad;\n\n  @include media('>large') {\n    padding-right: $pad-double;\n  }\n\n  > * {\n    margin-bottom: rem(15);\n  }\n}\n\n.footer__nav-link {\n  @include font--primary--s;\n\n  white-space: nowrap;\n\n  &:hover {\n    opacity: 0.8;\n  }\n}\n\n.footer__mailing {\n  max-width: rem(355);\n\n  input[type=\"text\"] {\n    background-color: transparent;\n  }\n}\n\n.footer__copyright {\n  text-align: left;\n  order: 1;\n\n  @include media('>large') {\n    order: 0;\n  }\n}\n\n.footer__social {\n  order: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  .icon {\n    padding: $pad-half;\n    display: block;\n    width: rem(40);\n    height: auto;\n\n    &:hover {\n      opacity: 0.8;\n    }\n  }\n}\n\n.footer__posts {\n  margin-top: $space;\n\n  @include media('>medium') {\n    margin-top: 0;\n  }\n}\n\n.footer__ads {\n  margin-top: $space-double;\n  width: rem(360);\n\n  @include media('>medium') {\n    display: none;\n  }\n\n  @include media('>xlarge') {\n    display: block;\n    margin-top: 0;\n  }\n}\n\n.footer__top {\n  position: absolute;\n  right: rem(-55);\n  bottom: rem(60);\n  padding: $pad-half $pad-half $pad-half $pad;\n  display: block;\n  width: rem(150);\n  transform: rotate(-90deg);\n  white-space: nowrap;\n\n  .icon {\n    height: auto;\n    transition: margin-left 0.25s ease;\n  }\n\n  &:hover {\n    .icon {\n      margin-left: $space;\n    }\n  }\n\n  @include media('>large') {\n    bottom: rem(70);\n  }\n}\n","/* ------------------------------------*\\\n    $HEADER\n\\*------------------------------------ */\n\n.header__utility {\n  display: flex;\n  height: rem($utility-header-height);\n  width: 100%;\n  position: fixed;\n  z-index: 99;\n  align-items: center;\n  flex-direction: row;\n  justify-content: space-between;\n  overflow: hidden;\n  border-bottom: 1px solid #4a4a4a;\n\n  a:hover {\n    opacity: 0.8;\n  }\n}\n\n.header__utility--left {\n  display: none;\n\n  @include media('>large') {\n    display: flex;\n  }\n}\n\n.header__utility--right {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n\n  @include media('>large') {\n    justify-content: flex-end;\n    width: auto;\n  }\n}\n\n.header__utility-search {\n  width: 100%;\n}\n\n.header__utility-mailing {\n  display: flex;\n  align-items: center;\n  padding-left: $pad-half;\n\n  .icon {\n    height: auto;\n  }\n}\n\n.header__utility-social {\n  display: flex;\n  align-items: flex-end;\n\n  a {\n    border-left: 1px solid #4a4a4a;\n    width: rem($utility-header-height);\n    height: rem($utility-header-height);\n    padding: $pad-half;\n\n    &:hover {\n      background-color: rgba(black, 0.8);\n    }\n  }\n}\n\n.header__nav {\n  position: relative;\n  width: 100%;\n  top: rem($utility-header-height);\n  z-index: 999;\n  background: $white;\n  height: rem($small-header-height);\n\n  @include media('>large') {\n    height: rem($large-header-height);\n    position: relative;\n  }\n\n  &.is-active {\n    .nav__primary-mobile {\n      display: flex;\n    }\n\n    .nav__toggle-span--1 {\n      width: rem(25);\n      transform: rotate(-45deg);\n      left: rem(-12);\n      top: rem(6);\n    }\n\n    .nav__toggle-span--2 {\n      opacity: 0;\n    }\n\n    .nav__toggle-span--3 {\n      display: block;\n      width: rem(25);\n      transform: rotate(45deg);\n      top: rem(-8);\n      left: rem(-12);\n    }\n\n    .nav__toggle-span--4::after {\n      content: \"Close\";\n    }\n  }\n}\n\n.header__logo-wrap a {\n  width: rem(100);\n  height: rem(100);\n  background-color: $white;\n  border-radius: 50%;\n  position: relative;\n  display: block;\n  overflow: hidden;\n  content: \"\";\n  margin: auto;\n  transition: none;\n\n  @include media('>large') {\n    width: rem(200);\n    height: rem(200);\n  }\n}\n\n.header__logo {\n  width: rem(85);\n  height: rem(85);\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  margin: auto;\n  display: block;\n\n  @include media('>large') {\n    width: rem(170);\n    height: rem(170);\n  }\n}\n","/* ------------------------------------*\\\n    $MAIN CONTENT AREA\n\\*------------------------------------ */\n\n.search .alm-btn-wrap {\n  display: none;\n}\n","/* ------------------------------------*\\\n    $ANIMATIONS & TRANSITIONS\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $BORDERS\n\\*------------------------------------ */\n\n.border {\n  border: 1px solid $border-color;\n}\n\n.divider {\n  height: rem(1);\n  width: rem(60);\n  background-color: $gray;\n  display: block;\n  margin: $space auto;\n  padding: 0;\n  border: none;\n  outline: none;\n}\n","/* ------------------------------------*\\\n    $COLOR MODIFIERS\n\\*------------------------------------ */\n\n/**\n * Text Colors\n */\n.color--white {\n  color: $white;\n  -webkit-font-smoothing: antialiased;\n}\n\n.color--off-white {\n  color: $off-white;\n  -webkit-font-smoothing: antialiased;\n}\n\n.color--black {\n  color: $black;\n}\n\n.color--gray {\n  color: $gray;\n}\n\n/**\n * Background Colors\n */\n.no-bg {\n  background: none;\n}\n\n.background-color--white {\n  background-color: $white;\n}\n\n.background-color--off-white {\n  background-color: $off-white;\n}\n\n.background-color--black {\n  background-color: $black;\n}\n\n.background-color--gray {\n  background-color: $gray;\n}\n\n/**\n * Path Fills\n */\n.path-fill--white {\n  path {\n    fill: $white;\n  }\n}\n\n.path-fill--black {\n  path {\n    fill: $black;\n  }\n}\n\n.fill--white {\n  fill: $white;\n}\n\n.fill--black {\n  fill: $black;\n}\n","/* ------------------------------------*\\\n    $DISPLAY STATES\n\\*------------------------------------ */\n\n/**\n * Completely remove from the flow and screen readers.\n */\n.is-hidden {\n  display: none !important;\n  visibility: hidden !important;\n}\n\n.hide {\n  display: none;\n}\n\n/**\n * Completely remove from the flow but leave available to screen readers.\n */\n.is-vishidden,\n.screen-reader-text,\